<style scoped>
    .pages {
        text-align: left;
        padding: 0 20px;

    }

    .form {
        width: 100%;
        margin-top: 30px;
    }
</style>

<template>
    <div class="pages">
        <H3>基础信息</H3>
        <div class="line"></div>
        <el-form class="form" ref="form" :model="form" label-width="120px">
            <el-form-item label="回顾标题">
                <el-input style="width: 500px" v-model="form.title" placeholder="请输入"></el-input>

            </el-form-item>
            <el-form-item label="封面图">
                <image-select v-model="img" :url="form.picture"></image-select>
            </el-form-item>
            <el-form-item label="回顾简介">
                <el-input

                        type="textarea"
                        style="width: 400px"
                        :rows="5"
                        v-model="form.content" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="推荐">
                <el-select class="header-col-select" v-model="form.recommend" placeholder="请选择">
                    <el-option
                            :value="1"
                            label="是">
                    </el-option>
                    <el-option
                            :value="0"
                            label="否">
                    </el-option>
                </el-select>
            </el-form-item>
            <div>
                <rich-view v-model="form.rich_text"></rich-view>
            </div>
            <el-form-item style="margin-top: 100px">
                <el-button type="primary" style="margin-left: 50px" :loading="isLoading"
                           @click="onSubmit">提交
                </el-button>
                <el-button @click="back">返回</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import api from '../../js/api/review'

    export default {
        data() {
            return {
                imageUrl: '',
                form: {
                    recomment: 0,
                    auditmethod: 0,
                },
                isNoEdit: true,
                isLoading: false,
                img: null,
            }
        },
        mounted() {
            if (this.$route.query.id !== undefined) {
                this.isNoEdit = true;
                this.loadData();
            } else {
                this.isNoEdit = false;
            }
        },
        methods: {
            loadData() {
                api.detail({id: this.$route.query.id}, res => {
                    this.form = res.data.data;
                })
            },
            edit() {
                this.isNoEdit = false;
            },
            onSubmit() {
                if (!api.isStrValid(this.form.title)) {
                    this.$message.error('请输入标题');
                    return;
                }
                if (!api.isStrValid(this.form.content)) {
                    this.$message.error('回顾简介');
                    return;
                }

                let data = api.objToFormData(this.form);

                if (this.img != null) {
                    data.append('picture', this.img);
                }
                this.isLoading = true;
                api.save(data, res => {
                    this.isLoading = false;
                    this.$message.success(res.data.message);
                    this.$router.push({name: 'review'})
                    this.$route.meta.keepAlive = false;
                }, () => {
                    this.isLoading = false;
                })
            },
            back() {
                this.$router.back();
            }

        }
    }
</script>
